<template>
  <div @touchmove="backTop">
    <form action="/" class="search_top">
        <van-search
            v-model="value"
            placeholder="请输入搜索关键词"
            @search="onSearch"
            show-action
            aria-autocomplete=""
            @focus="focus(value)"
            @cancel="onCancel"
            @input="input"
            @blur="blur"
            autofocus
        >
        </van-search>
    </form>
    <div class="box_history"  v-if="ishistory">
      <div class="suosou_history">
        <p>搜索历史</p>
        <van-icon name="delete-o" @click="del"/>
      </div>
      <div >
        <!-- <span >{{item}}</span> -->
        <van-cell :title="item" is-link v-for="(item,i) in arr" :key="i" class="history" @click="sousuohistory(item)">
          <template #right-icon>
            <van-icon name="cross" size='20' @click.stop="delete_history(i)"/>
          </template>
        </van-cell>
      </div>
    </div>
    <div v-else  v-show="isshow">
      <van-cell :title="item.title" v-for="(item,i) in list" :key="i" class="history" @click="sousuohistory(item.title)">
      </van-cell>
    </div>
    <!-- 搜索结果 -->
     <sousuo-list :val=val  v-show="sou" class="leibiaojieguo"/>
     <!-- 返回顶部 -->
    <a href="#" v-show="isShow1" @click="top" class="top">
      <van-icon name="back-top" size='25' class="topimg"/>
    </a>
  </div>
</template>
 
<script>
import sousuoList from '@/views/sousuo/sousuoList.vue'
import { Toast, Search,Icon,Cell, CellGroup  } from 'vant';
export default {
  name:'search',
  data() {
    return {
        value: '',
        arr:[],
        sou:false,
        val:'',
        ishistory:true,
        list:[],
        isshow:false,
        isShow1:false
    }
  },
  components:{
    sousuoList,
    [Toast.name]:Toast,
    [Search.name]:Search,
    [Icon.name]:Icon,
    [Cell.name]:Cell,
    [CellGroup.name]:CellGroup
  },
  methods: {
    onSearch(value) {
      // console.log(value);
      if(value.trim().length===0){
        // this.ishistory=true
      }else{ 
        this.ishistory=false
        this.isshow=false
        this.sou=true
        this.arr.unshift(value.trim())
        this.arr= [...new Set(this.arr)]
        this.val=value.trim() 
        localStorage.setItem('sousuohistory',this.arr)
        
       
      }
    },
    input(){
      //  console.log(11);
       this.ishistory=false
       this.isshow=true
       this.list=[]
       if(this.value.trim().length===0){
        this.ishistory=true
       }else{
        //  setTimeout(()=> {
           this.$axios({
             method: 'post',
             url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/search',
             data: {
               "key_word": this.value,
               "skip": this.list.length,
               "limit": 15,
             }
           }).then(res => {
             if(this.value.trim().length===0){
               

             }
             this.list.push(...res.data.data) ;
             // this.list=res.data.data
             this.loading = false;
            //  console.log(res);
             // console.log('list',this.list.length);
             // 数据全部加载完成
             if (this.list.length >= res.data.count){

                 this.finished = true
               };
           })
           // console.log("123",this.skip);
           this.finished = false;
           // 加载状态结束

        //  },700);
       }
    },
    onCancel() {
        this.list=[]
        this.$router.push('/home');
        this.value= '';
        this.val=''
        this.ishistory=true
    },
    del(){
      this.arr=[]
      localStorage.setItem('sousuohistory',this.arr)
    },
    focus(){
      this.ishistory=false
      this.sou=false
      this.isshow=true
      if(this.value.trim().length===0){
        this.ishistory=true
        this.isshow=false
      }
      this.gangjin()
      
    },
    blur(){
      // this.isshow=false
      // this.sou=true
    },
    sousuohistory(item){
      //  console.log(item);
      // this.isshow=true
       this.value=item
       this.onSearch(item)
    },
    delete_history(i){
      this.arr.splice(i,1);
      localStorage.setItem('sousuohistory',this.arr)
      // console.log(111);
    },
    backTop(e){ 
      // console.log(1);
      if (e.view.pageYOffset >= 1500) {
        this.isShow1 = true;
       
      }else{
         this.isShow1 = false
      }
      // console.log(e.view.pageYOffset);
    },
    top(){
      // console.log(document.documentElement.scrollTop);
      this.isShow1 = false
    },
    gangjin(){
      if(this.value.trim().length===0){

      }else{
            this.$axios({
             method: 'post',
             url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/search',
             data: {
               "key_word": this.value,
               "skip": this.list.length,
               "limit": 15,
             }
           }).then(res => {
             
             this.list.push(...res.data.data) ;
             // this.list=res.data.data
             this.loading = false;
            //  console.log(res);
             // console.log('list',this.list.length);
             // 数据全部加载完成
             if (this.list.length >= res.data.count){

                 this.finished = true
               };
           })
           // console.log("123",this.skip);
           this.finished = false
      };
    }
  },
  mounted(){
    let sousuohistory=localStorage.getItem('sousuohistory')
    // console.log(sousuohistory.length);
    // if (sousuohistory.length===0) {
      
    // }else{
      
    // }
    this.arr = sousuohistory.split(',');
    this.isshow=true
    //split可将字符串分割成数组
    // this.arr = this.arr.filter(val=>val!==',') 
    // console.log(localStorage.getItem('sousuohistory').split(','));
    // console.log(localStorage.getItem('sousuohistory'));
    // console.log(this.arr);
    this.value= '';
     
     
    
  },
  created(){
    
  
  },
  activated(){
    //  console.log(document.querySelector('.van-field__control').focus);
    //  document.querySelector('.van-field__control').focus()
  },
}
</script>

<style lang="less" scoped>
.search_top{
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
}
.box_history{
  margin-top: 55px;
}
.suosou_history{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 5px;
  padding: 0 10px;
  p{
    font-size: 10px;
  }
}
.history{
  border-bottom:1px solid #ccc ;
}
.leibiaojieguo{
  margin-top: 60px;
}
</style>